
import { Vue } from "vue-class-component";
import { Emit, Prop } from "vue-property-decorator";

export default class CardButton extends Vue {
  @Prop({ default: false }) readonly disabled!: boolean;
  @Prop(String) readonly label!: string;

  @Emit() onClick() {
    return;
  }

  onClickIfEnabled() {
    if (!this.disabled) {
      this.onClick();
    }
  }
}
